<template>
    <div class="public_content">
       <div class="public_title"><span>委托房源</span></div>
       <!-- 搜索 -->
       <div class="authorized"> 
       		<el-select v-model="type" placeholder="全部">
            <el-option
              v-for="item in types"
              :key="item.id"
              :label="item.paramname"
              :value="item.id">
            </el-option>
  			  </el-select>

			    <v-address class="inlineBlock"  :showAddress='false' @emitChange='emitChange' :showLocation='true'/>


          <el-select v-model="time" placeholder="请选择时间">
            <el-option
            v-for="item in timeSelect"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
          </el-select>
          <el-input style="width: 280px;" placeholder="请输入小区关键词" v-model="keywords"> 
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
       </div>

        <!-- table -->
  			<el-table :data="tableData" stripe style="width: 100%;height: 500px" >
    			<el-table-column prop="createtime" label="日期">
            <template slot-scope="scope">
              {{scope.row.createtime | formatDate}}
            </template>
    			</el-table-column>
    			<el-table-column label="基本信息"> 
            <template slot-scope="scope"> 
               地址
               {{scope.row.area + '㎡ | '}}
               {{scope.row.prices + '元/月'}} 
            </template>
          </el-table-column>
    			<el-table-column prop="detailed" label="详细信息">
      				<template slot-scope="scope">
      					<el-button type="text" @click="watchData(scope.row)">点击查看</el-button>
      				</template>
    			</el-table-column>
  			</el-table>


        <!-- 弹窗 -->
        <el-dialog title="委托详情——求购" :visible.sync="dialogFormVisible">
            <p class="detail-item"><span class="label">小区名称</span><span class="title">弘扬</span></p>
            <p class="detail-item"><span class="label">小区位置</span><span>莫某地方</span></p>
            <p class="detail-item">
              <span class="label">房屋类型</span><span class="cont">公寓</span>
              <span class="label">装修程度</span><span class="cont">精装</span>
            </p>
             <p class="detail-item">
              <span class="label">房屋朝向</span><span class="cont">朝南</span>
              <span class="label">房屋户型</span>
              <span class="cont">
                {{nowData.bedroom ? nowData.bedroom + '室': ''}}
                {{nowData.bedroom ? nowData.livingroom + '卫': ''}} 
                {{nowData.bedroom ? nowData.bathroom + '厅': ''}} 
              </span>
            </p>
            <p class="detail-item">
              <span class="label">期望面积</span><span class="cont">122</span>
              <span class="label">期望售价</span><span class="cont" style="color: #00a06b;">121万</span>
            </p>
            <p class="detail-item">
              <span class="label">发布人</span><span class="cont">张</span>
              <span class="label">联系电话</span><span class="cont" style="color: rgb(244, 72, 100);">1223434</span>
            </p>
        </el-dialog>  

  			<div style="text-align: right">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="page.pageNo"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="page.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total">
          </el-pagination> 
  			</div>
      	
    </div>
</template>

<script>
import { getTrustList } from "@/api/entrust";
import vAddress from "@/components/address/index";

export default {
  components: {
    vAddress
  },
  data() {
    return {
      dialogFormVisible: true,
      keywords: "",
      type: "", //1 求租 2求购 3出租 4出售 5转让  默认出售 4
      region: "",
      estate: "",
      time: "",
      timeSelect: [],
      types: [
        {
          id: "",
          paramname: "全部"
        },
        {
          id: "1",
          paramname: "出售"
        }
      ],
      page: {
        total: 0,
        pageNO: 1,
        pageSize: 10
      },
      tableData: [],
      nowData: {}
    };
  },
  created() {
    this.fetchdata();
  },
  methods: {
    fetchdata() {
      let data = {
        pageNO: this.page.pageNO,
        pageSize: this.page.pageSize,
        type: this.type,
        region: this.region,
        estate: this.estate
      };
      getTrustList(data).then(res => {});
      this.tableData = [
        {
          id: 4,
          createuser: "user4",
          createtel: "19439435943",
          community_id: 15,
          address: null,
          type: 1,
          house_type: null,
          area: "128",
          prices: "0",
          bedroom: 0,
          livingroom: null,
          bathroom: null,
          fixture: null,
          pay_type: null,
          towards: null,
          tag: null,
          createtime: 1542762555,
          status: 1,
          is_del: 0,
          deal_time: 1544514916,
          estate: null,
          region: null
        }
      ];
    },
    watchData(val) {
      this.dialogFormVisible = true;
      this.nowData = val;
    },
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.fetchData();
    },
    handleCurrentChange(val) {
      this.page.pageNo = val;
      this.fetchData();
    },
    /* 地址接收 */
    emitChange(data) {
      this.region = data.region;
      this.estate = data.estate;
    }
  }
};
</script>

<style lang='scss' scoped>
.authorized {
  width: 100%;
  padding: 10px;
  background-color: #f0f2f5;
}
.detail-item {
  min-height: 50px;
  line-height: 50px;
  & + & {
    border-top: 1px solid #f3f3f3;
  }
  .title {
    display: inline-block;
    width: 618px;
    vertical-align: top;
    color: #36a3f7;
  }
  .label {
    display: inline-block;
    width: 78px;
    color: #666;
  }
  .cont {
    display: inline-block;
    width: 274px;
  }
}
</style>



